import Dimension from '../dimension';
import { bounds } from '../../utils/geo';
import InvalidAwareTypes from '../../invalid-aware-types';
import { rowDiffsetIterator } from '../../operator/row-diffset-iterator';

/**
 * Represents geo field subtype.
 *
 * @public
 * @class
 * @extends Dimension
 */
export default class GeoField extends Dimension {
    /**
     * Initialize a new instance.
     *
     * @public
     * @param {PartialField} partialField - The partialField instance which holds the whole data.
     * @param {string} rowDiffset - The data subset definition.
     */
    constructor (partialField, rowDiffset) {
        super(partialField, rowDiffset);

        this._cachedBounds = null;
    }

    /**
     * Gets the coordinates of all the geofields.
     * @return {Array} coordinates
     */
    getCoordinates () {
        const coordinates = [];
        rowDiffsetIterator(this.rowDiffset, (i) => {
            const datum = this.partialField.data[i];
            coordinates.push(datum.getCoordinates());
        });

        return coordinates;
    }

    /**
     * Caches and returns the bounds for the geo field.
     * @return {Array} Result of this.bounds
     *
     * @public
     */
    calculateDataDomain () {
        if (!this._cachedBounds) {
            this._cachedBounds = this.bounds();
        }
        return this._cachedBounds;
    }

    /**
     * Returns bounds for all of the geo fields.
     * @return {Array} Array of bounds
     *
     * @public
     */
    bounds () {
        if (this._cachedBounds) {
            return this._cachedBounds;
        }

        const boundsArr = [];
        rowDiffsetIterator(this.rowDiffset, (i) => {
            const datum = this.partialField.data[i];

            if (!InvalidAwareTypes.isInvalid(datum)) {
                boundsArr.push(datum.bounds());
            }
        });

        const boundsFlattened = [].concat(...boundsArr);
        this._cachedBounds = bounds(boundsFlattened);
        return this._cachedBounds;
    }
}
