import Geometry from '../geometry';
import Point from '../point';
import { bounds, center } from '../../utils/geo';
import { GeomTypes } from '../../enums/geom-types';

/**
 * Represents MultiPoint.
 *
 * @public
 * @class
 */
export default class MultiPoint extends Geometry {
    /**
     * Initialize a new instance of MultiPoint.
     *
     * @public
     * @param {Object} value - Value type value
     * @param {Array} value.coordinates - The coordinates of the multiPoint
     * @param {string} value.type - The coordinates of the multiPoint
     */
    constructor (value) {
        super(value);
        this._realCoordinates = value.coordinates;
        this.coordinates = value.coordinates.map(coordinate => new Point({
            type: GeomTypes.Point,
            coordinates: coordinate
        }));
    }

    /**
     * Get co-ordinates of the multiPoint.
     *
     * @public
     * @return {Array} array of coordinates
     */
    getCoordinates () {
        return this._realCoordinates;
    }

    /**
     * Get bounds box of the multiPoint.
     *
     * @public
     * @param {Array} coordinates coordinates of the multiPoint
     * @return {Array} an array of format [[minX, minY],[maxX, maxY]]
    */
    bounds () {
        let boundsArr = this._cachedBounds;

        if (!boundsArr) {
            const multiPointData = this.coordinates.map(point => point.coordinates);
            boundsArr = this._cachedBounds = bounds(multiPointData);
        }

        return boundsArr;
    }

    /**
     * Get area of the multi point.
     *
     * @public
     * @return {number} area of the multi point
     */
    area () {
        return 0;
    }

    /**
     * Get center of the multiPoint.
     *
     * @public
     * @return {Array} center of the bounds
     */
    center () {
        return center(this._realCoordinates);
    }

    /**
     * Returns true if a point is in multiline.
     *
     * @name includes
     * @param {Array.<number>} point point Coordinates of form [x, y]
     * @return {boolean} true/false
     */
    includes (point) {
        let output = false;
        const mPointCoord = this.getCoordinates();
        for (let i = 0; i < mPointCoord.length; i++) {
            if (mPointCoord[i][0] === point[0] && mPointCoord[i][1] === point[1]) {
                output = true;
                break;
            }
        }
        return output;
    }
}
