import { GEO_ERR_MESSAGES } from '../../../../enums/error-messages';
import { checkPointCoordinates } from '../point';
import MultiPoint from '../../../../geo/multi-point';

/**
 * Returns a new MultiPoint instance if the coordinates are valid. Throws error if coordinates are invalid or
 * the structure is not proper.
 *
 * @param {Object} val
 * @param {string} val.type Type of geometry
 * @param {Array} val.coordinates Coordinates array.
 *
 * @return {MultiPoint} Instance of MultiPoint.
 */
export const multiPointParser = (val) => {
    const coordinates = val.coordinates;

    if (!Array.isArray(coordinates)) {
        throw new Error(GEO_ERR_MESSAGES.isArray);
    }

    coordinates.forEach((coord) => {
        checkPointCoordinates(coord);
    });

    return new MultiPoint(val);
};
