import DistanceUnits from './distance-units';

/**
 * Earth Radius used with the Harvesine formula and approximates using a spherical (non-ellipsoid) Earth.
 *
 * @memberof helpers
 * @type {number}
 */
export const earthRadius = 6371008.8;

/**
 * Unit of measurement factors using a spherical (non-ellipsoid) earth radius.
 *
 * @memberof helpers
 * @type {Object}
 */
export const UNITS = {
    CENTIMETERS: 'centimeters',
    DEGREES: 'degrees',
    FEET: 'feet',
    INCHES: 'inches',
    KILOMETERS: 'kilometers',
    METERS: 'meters',
    METRES: 'metres',
    MILES: 'miles',
    MILLIMETERS: 'millimeters',
    NAUTICALMILES: 'nauticalmiles',
    RADIANS: 'radians',
    YARDS: 'yards',
};

/**
 * Unit of measurement factors using a spherical (non-ellipsoid) earth radius.
 *
 * @memberof helpers
 * @type {Object}
 */
export const factors = {
    [DistanceUnits.CENTIMETERS]: earthRadius * 100,
    [DistanceUnits.DEGREES]: earthRadius / 111325,
    [DistanceUnits.FEET]: earthRadius * 3.28084,
    [DistanceUnits.INCHES]: earthRadius * 39.370,
    [DistanceUnits.KILOMETERS]: earthRadius / 1000,
    [DistanceUnits.METERS]: earthRadius,
    [DistanceUnits.METRES]: earthRadius,
    [DistanceUnits.MILES]: earthRadius / 1609.344,
    [DistanceUnits.MILLIMETERS]: earthRadius * 1000,
    [DistanceUnits.NAUTICALMILES]: earthRadius / 1852,
    [DistanceUnits.RADIANS]: 1,
    [DistanceUnits.YARDS]: earthRadius / 1.0936,
};
