import { rowDiffsetIterator } from './row-diffset-iterator';
import InvalidAwareTypes from '../invalid-aware-types';
import { includes, bounds } from '../utils/geo';
import RTree from '../utils/r-tree';
import { GeomTypes } from '../enums';
import { GEO_BIN_SHAPES } from '../enums/geo-bin-shapes';
import { hexbin } from './hexbin';

const generateBuckets = (binSize, start, end) => {
    const buckets = [];
    let next = start;

    while (next < end) {
        buckets.push(next);
        next += binSize;
    }
    buckets.push(next);

    return buckets;
};

const findBucketRange = (bucketRanges, value) => {
    let leftIdx = 0;
    let rightIdx = bucketRanges.length - 1;
    let midIdx;
    let range;

    // Here use binary search as the bucketRanges is a sorted array
    while (leftIdx <= rightIdx) {
        midIdx = leftIdx + Math.floor((rightIdx - leftIdx) / 2);
        range = bucketRanges[midIdx];

        if (value >= range.start && value < range.end) {
            return range;
        } else if (value >= range.end) {
            leftIdx = midIdx + 1;
        } else if (value < range.start) {
            rightIdx = midIdx - 1;
        }
    }

    return null;
};

 /**
  * Creates the bin data from input measure field and supplied configs.
  *
  * @param {Measure} measureField - The Measure field instance.
  * @param {string} rowDiffset - The datamodel rowDiffset values.
  * @param {Object} config - The config object.
  * @return {Object} Returns the binned data and the corresponding bins.
  */
export function createBinnedFieldData (measureField, rowDiffset, config) {
    let { buckets, binsCount, binSize, start, end } = config;
    const [dMin, dMax] = measureField.domain();

    if (!buckets) {
        start = (start !== 0 && (!start || start > dMin)) ? dMin : start;
        end = (end !== 0 && (!end || end < dMax)) ? (dMax + 1) : end;

        if (binsCount) {
            binSize = Math.ceil(Math.abs(end - start) / binsCount);
        }

        buckets = generateBuckets(binSize, start, end);
    }

    if (buckets[0] > dMin) {
        buckets.unshift(dMin);
    }
    if (buckets[buckets.length - 1] <= dMax) {
        buckets.push(dMax + 1);
    }

    const bucketRanges = [];
    for (let i = 0; i < buckets.length - 1; i++) {
        bucketRanges.push({
            start: buckets[i],
            end: buckets[i + 1]
        });
    }

    const binnedData = [];
    rowDiffsetIterator(rowDiffset, (i) => {
        const datum = measureField.partialField.data[i];
        if (datum instanceof InvalidAwareTypes) {
            binnedData.push(datum);
            return;
        }

        const range = findBucketRange(bucketRanges, datum);
        binnedData.push(`${range.start}-${range.end}`);
    });

    return { binnedData, bins: buckets };
}

const bucketCreators = {
    [GEO_BIN_SHAPES.HEX]: hexbin
};

export const createBinnedGeoData = (field, rowDiffset, config) => {
    let { buckets } = config;
    const { binSize, shapeType, units } = config;
    const { data } = field.partialField;

    if (!buckets) {
        const bucketCreatorFn = bucketCreators[shapeType];

        if (shapeType in bucketCreators) {
            buckets = bucketCreatorFn(field.bounds(), binSize, { units });
        } else {
            throw new Error('Unsupported shape for binning');
        }
    }

    const binnedData = [];
    const points = [];
    rowDiffsetIterator(rowDiffset, (i) => {
        const datum = data[i];
        if (!(datum instanceof InvalidAwareTypes)) {
            points.push({
                geometry: datum,
                id: i
            });
        }
        binnedData[i] = null;
    });

    const rtree = new RTree(6);

    const treeItems = points.map((item) => {
        const coords = item.geometry.center();
        return {
            minX: coords[0],
            minY: coords[1],
            maxX: coords[0],
            maxY: coords[1],
            id: item.id
        };
    });

    rtree.load(treeItems);
    const { Polygon } = GeomTypes;

    buckets.forEach((poly) => {
        const coordinates = poly.coordinates.flat();
        const bbox = bounds(coordinates).flat();
        const potentialPoints = rtree.search({ minX: bbox[0], minY: bbox[1], maxX: bbox[2], maxY: bbox[3] });

        potentialPoints.forEach((pt) => {
            if (includes(coordinates, [pt.minX, pt.minY])) {
                binnedData[pt.id] = {
                    center: poly.center,
                    side: poly.side,
                    radius: poly.radius,
                    type: Polygon,
                    coordinates: poly.coordinates
                };
            }
        });
    });

    return binnedData;
};
