import { GEO_ERR_MESSAGES } from '../../../../enums/error-messages';
import { checkPointCoordinates } from '../point';
import LineString from '../../../../geo/line-string';

/**
 * Checks the line string coordinates and throws if any invalid values are given.
 *
 * @param {Array} coordinates Array of coordinates.
 */
export const checkLineStringCoordinates = (coordinates) => {
    if (!Array.isArray(coordinates)) {
        throw new Error(GEO_ERR_MESSAGES.isArray);
    }
    if (!(coordinates.length > 1)) {
        throw new Error(GEO_ERR_MESSAGES.hasMinimumPoints);
    }

    coordinates.forEach((coord) => {
        checkPointCoordinates(coord);
    });

    return true;
};

/**
 * Returns a new LineString instance if the coordinates are valid. Throws error if coordinates are invalid or
 * the structure is not proper.
 *
 * @param {Object} val
 * @param {string} val.type Type of geometry
 * @param {Array} val.coordinates Coordinates array.
 *
 * @return {LineString} Instance of LineString.
 */
export const lineParser = (val) => {
    const coordinates = val.coordinates;

    checkLineStringCoordinates(coordinates);

    return new LineString(val);
};
