import { distance } from '../utils/geo';

/**
 * Creates hexagon
 *
 * @private
 * @param {Array<number>} center of the hexagon
 * @param {number} rx half hexagon width
 * @param {number} ry half hexagon height
 * @param {Object} properties passed to each hexagon
 * @param {Array<number>} cosines precomputed
 * @param {Array<number>} sines precomputed
 * @returns {Feature<Polygon>} hexagon
 */
const hexagon = (center, rx, ry, cosines, sines) => {
    const vertices = [];
    for (let i = 0; i < 6; i++) {
        const x = center[0] + rx * cosines[i];
        const y = center[1] + ry * sines[i];
        vertices.push([x, y]);
    }
    const radius = distance(center, vertices[0], 'kilometers');
    // first and last vertex must be the same
    vertices.push(vertices[0].slice());
    return {
        coordinates: [vertices],
        center,
        radius
    };
};

/**
 * Takes a bounding box and the diameter of the cell and returns an array of flat-topped
 * hexagons or triangles aligned in an "odd-q" vertical grid as
 * described in [Hexagonal Grids]
 *
 * @name hexbin
 * @param {BBox} bbox extent in [minX, minY, maxX, maxY] order
 * @param {number} cellSide length of the side of the the hexagons or triangles, in units.
 * It will also coincide with the radius of the circumcircle of the hexagons.
 * @param {Object} [options={}] Optional parameters
 * @param {string} [options.units='kilometers'] used in calculating cell size, can be degrees, radians, miles,
 * or kilometers
 * @param {Object} [options.properties={}] passed to each hexagon or triangle of the grid
 * @param {Feature<Polygon>} [options.mask] if passed a Polygon or MultiPolygon, the grid Points will be created
 * only inside it.
 * @param {boolean} [options.triangles=false] whether to return as triangles instead of hexagons
 * @returns {FeatureCollection<Polygon>} a hexagonal grid
 * @example
 * var bbox = [-96,31,-84,40];
 * var cellSide = 50;
 * var options = {units: 'miles'};
 *
 * var bins = hexbin(bbox, cellSide, options);
 */
export const hexbin = (bbox, cellSide, options) => {
    const [west, south, east, north] = bbox.flat();
    const centerY = (south + north) / 2;
    const centerX = (west + east) / 2;
    const xFraction = cellSide * 2 / (distance([west, centerY], [east, centerY], options.units));
    const cellWidth = xFraction * (east - west);
    const yFraction = cellSide * 2 / (distance([centerX, south], [centerX, north], options.units));
    const cellHeight = yFraction * (north - south);
    const radius = cellWidth / 2;

    const hexWidth = radius * 2;
    const hexHeight = Math.sqrt(3) / 2 * cellHeight;

    const boxWidth = east - west;
    const boxHeight = north - south;

    const xInterval = 3 / 4 * hexWidth;
    const yInterval = hexHeight;

    // adjust boxWidth so all hexagons will be inside the bbox
    const xSpan = (boxWidth - hexWidth) / (hexWidth - radius / 2);
    const xCount = Math.floor(xSpan);

    const xAdjust = ((xCount * xInterval - radius / 2) - boxWidth) / 2 - radius / 2 + xInterval / 2;

    // adjust boxHeight so all hexagons will be inside the bbox
    const yCount = Math.floor((boxHeight - hexHeight) / hexHeight);

    let yAdjust = (boxHeight - yCount * hexHeight) / 2;

    const hasOffsetY = yCount * hexHeight - boxHeight > hexHeight / 2;
    if (hasOffsetY) {
        yAdjust -= hexHeight / 4;
    }

    const cosines = [];
    const sines = [];
    for (let i = 0; i < 6; i++) {
        const angle = 2 * Math.PI / 6 * i;
        cosines.push(Math.cos(angle));
        sines.push(Math.sin(angle));
    }

    const results = [];
    for (let x = 0; x <= xCount; x++) {
        for (let y = 0; y <= yCount; y++) {
            const isOdd = x % 2 === 1;
            if (y === 0 && isOdd) continue;
            if (y === 0 && hasOffsetY) continue;

            const xCenter = x * xInterval + west - xAdjust;
            let yCenter = y * yInterval + south + yAdjust;

            if (isOdd) {
                yCenter -= hexHeight / 2;
            }

            const hex = hexagon(
                [xCenter, yCenter],
                cellWidth / 2,
                cellHeight / 2,
                cosines,
                sines
            );

            results.push(hex);
        }
    }

    return results;
};

