import {
    isArrEqual,
    flattenArrayToSingleLevel,
    radiansToLength,
    degreesToRadians
} from './helper';
import { geometryMap } from '../geo';
import { GeomTypes, DistanceUnits } from '../enums';

/**
 * inRing
 *
 * @private
 * @param {Array<number>} pt [x,y]
 * @param {Array<Array<number>>} ring [[x,y], [x,y],..]
 * @param {boolean} ignoreBoundary ignoreBoundary
 * @return {boolean} inRing
 */
const inRing = (pt, ring, ignoreBoundary) => {
    try {
        let isInside = false;
        if (ring[0][0] === ring[ring.length - 1][0] && ring[0][1] === ring[ring.length - 1][1]) {
            ring = ring.slice(0, ring.length - 1);
        }
        for (let i = 0, j = ring.length - 1; i < ring.length; j = i++) {
            const xi = ring[i][0];
            const yi = ring[i][1];
            const xj = ring[j][0];
            const yj = ring[j][1];
            const onBoundary = (pt[1] * (xi - xj) + yi * (xj - pt[0]) + yj * (pt[0] - xi) === 0) &&
                ((xi - pt[0]) * (xj - pt[0]) <= 0) && ((yi - pt[1]) * (yj - pt[1]) <= 0);
            if (onBoundary) {
                return !ignoreBoundary;
            }
            const intersect = ((yi > pt[1]) !== (yj > pt[1])) &&
                (pt[0] < (xj - xi) * (pt[1] - yi) / (yj - yi) + xi);
            if (intersect) {
                isInside = !isInside;
            }
        }
        return isInside;
    } catch (err) {
        throw new Error('Wrong input for polygon');
    }
};

/**
 * Takes a set of coordinates, returns the geographical boundaries from all the coordinates in the data.
 *
 * @name bounds
 * @param {Array} coordinates any GeoJSON type array
 * @return {Array} [
 [lon, lat], // Represents the longitude and latitude of the southwest point.
 [lon, lat] // Represents the longitude and latitude of the northeast point.
]
*/
export const bounds = (coordinates) => {
    let boundArray = [[Infinity, Infinity], [-Infinity, -Infinity]];
    Array.isArray(coordinates) && coordinates.forEach((coordinate) => {
        if (boundArray[0][0] > coordinate[0]) { boundArray[0][0] = coordinate[0]; }
        if (boundArray[0][1] > coordinate[1]) { boundArray[0][1] = coordinate[1]; }
        if (boundArray[1][0] < coordinate[0]) { boundArray[1][0] = coordinate[0]; }
        if (boundArray[1][1] < coordinate[1]) { boundArray[1][1] = coordinate[1]; }
    });
    return boundArray;
};

/**
 * Takes an array of points and returns the centroid
 *
 * @name centroid
 * @param {Array} points an array of points
 * @return {Array} the centroid
 */
export const centroid = (points) => {
    let sumX = 0;
    let sumY = 0;
    let count = points.length;
    for (let i in points) {
        sumX = +points[i][0];
        sumY = +points[i][1];
    }
    return count ? ([
        Math.round((sumX / count) * 100) / 100,
        Math.round((sumY / count) * 100) / 100
    ]) : [0, 0];
};

/**
 * Takes an array of points and returns the center
 *
 * @name center
 * @param {Array} points an array of points
 * @return {Array} the center
 */
export const center = (points) => {
    let centerPoint = [0, 0];
    if (points[0] && points[0].length === 2) {
        const boundsData = bounds(points);
        const x = (boundsData[0][0] + boundsData[1][0]) / 2;
        const y = (boundsData[0][1] + boundsData[1][1]) / 2;
        centerPoint = [x, y];
    }
    return centerPoint;
};

/**
 * Get area of the Polygon.
 *
 * @param {Array} vertices the coordinates of the area
 * @return {number} the are of the polygon with the given coordinates
 */
export const area = (vertices) => {
    let total = 0;
    let addX;
    let addY;
    let subX;
    let subY;
    for (let i = 0, l = vertices.length; i < l; i++) {
        addX = vertices[i][0];
        addY = vertices[i === vertices.length - 1 ? 0 : i + 1][1];
        subX = vertices[i === vertices.length - 1 ? 0 : i + 1][0];
        subY = vertices[i][1];

        total += (addX * addY * 0.5);
        total -= (subX * subY * 0.5);
    }
    return Math.round(Math.abs(total) * 100) / 100;
};

export const isValidLatitude = val => val >= -90 && val <= 90;
export const isValidLongitude = val => val >= -180 && val <= 180;

export const latLongPredicates = {
    0: isValidLongitude,
    1: isValidLatitude
};

/**
 * Compares two geoShapes for equality
 * First bounds of both shapes and checked, if they match, coordinates are matched.
 * @param {Object} geoField1 Instance of first geoField
 * @param {Object} geoField2 Instance of second geoField
 * @return {boolean} Returns true if both shapes are same, false otherwise.
 */
const compare = (geoField1, geoField2) => {
    let geoField1Coords;
    let geoField2Coords;
    const geoField1Bounds = geoField1.bounds();
    const geoField2Bounds = geoField2.bounds();
    const isBoundsEqual = geoField1Bounds.every((g, index) => isArrEqual(g, geoField2Bounds[index]));

    if (!isBoundsEqual) return false;
    // Flatten coordinates only if bounds are equal
    geoField1Coords = flattenArrayToSingleLevel(geoField1.getCoordinates());
    geoField2Coords = flattenArrayToSingleLevel(geoField2.getCoordinates());

    switch (geoField1.type) {
    case GeomTypes.Point:
        return isBoundsEqual;
    case GeomTypes.MultiPoint:
    case GeomTypes.LineString:
    case GeomTypes.MultiLineString:
    case GeomTypes.Polygon:
    case GeomTypes.MultiPolygon:
        return isBoundsEqual ?
            geoField1Coords.every((g, i) => isArrEqual(g, geoField2Coords[i])) : false;
    default:
        return false;
    }
};

/**
 * Checks if two geoShapes are equal
 * @param {Object} geoField1 first geo shape
 * @param {Object} geoField2 second geo shape
 * @return {boolean} true if shapes are equal, false otherwise
 */
export const isSame = (geoField1, geoField2) => {
    if (!geoField1 || !geoField2) return false;

    const type1 = geoField1.type;
    const type2 = geoField2.type;

    if (type1 !== type2) return false;
    return compare(geoField1, geoField2);
};

/**
 * Merges two coordinates of same geometry type
 * @param {Array} coordsArr Coordinates of shapes
 * @param {string} type Geometry type
 * @return {Array} Returns the combined coordinates
 */
const getMergedCoords = (coordsArr, type) => {
    switch (type) {
    case GeomTypes.Point:
    case GeomTypes.LineString:
    case GeomTypes.Polygon:
        return [].concat(coordsArr);
    case GeomTypes.MultiPoint:
    case GeomTypes.MultiLineString:
    case GeomTypes.MultiPolygon:
        return [].concat(...coordsArr);
    default:
        throw new Error('Invalid Geometry Type');
    }
};

/**
 * Combines two geometries of the same type
 * @param {Object} geoField1 first geo shape
 * @param {Object} geoField2 second geo shape
 * @return {Object} Returns the new combined geometry shape instance
 */
export const geoUnion = (...geometry) => {
    if (geometry.some(geom => !geom) || geometry.length < 2) {
        throw new Error('One or more geometry fields missing');
    }
    if (geometry.some(geom => geom.type !== geometry[0].type)) {
        throw new Error('All geometries must be of the same type');
    }
    const geomType = geometry[0].type;
    let newGeoType = geomType;

    // If geomType is point/lineString/polygon, we need to return a multiPoint/multiLineString/multiPolygon
    if ([GeomTypes.Point, GeomTypes.LineString, GeomTypes.Polygon].includes(geomType)) {
        newGeoType = `Multi${geomType}`;
    }
    // Prepare data for combined geometry
    const data = {
        type: newGeoType,
        coordinates: getMergedCoords(geometry.map(geom => geom.getCoordinates()), geomType)
    };

    // Prepare and return the geometry
    return new geometryMap[newGeoType](data);
};

/**
 * Calculates the distance between two points in degrees, radians, miles, or kilometers.
 * This uses the [Haversine formula](http://en.wikipedia.org/wiki/Haversine_formula) to account for global curvature.
 *
 * @name distance
 * @param {Array.<Number>} from origin point
 * @param {Array.<Number>} to destination point
 * @param {string} units can be degrees, radians, miles, or kilometers
 * @returns {number} distance between the two points
 * @example
 * const from = [-75.343, 39.984];
 * const to = [-75.534, 39.123];
 * const unit = 'miles';
 *
 */
export const distance = (from, to, unit) => {
    const dLat = degreesToRadians((to[1] - from[1]));
    const dLon = degreesToRadians((to[0] - from[0]));
    const lat1 = degreesToRadians(from[1]);
    const lat2 = degreesToRadians(to[1]);

    const a = Math.sin(dLat / 2) ** 2 + Math.sin(dLon / 2) ** 2
    * Math.cos(lat1) * Math.cos(lat2);

    return radiansToLength(2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a)), unit);
};

/**
 * Finds out the nearest point to a given coordinate
 * @param {Object} geoFieldsCoordinates Array of coordinates
 * @param {Array} referencePoint Coordinate Array for which nearestPoint has to be found in [lon, lat]
 * @return {Array} nearestPoint - Returns the nearestPoint in [lon, lat]
 */
export const nearestPoint = (geoFieldsCoordinates, referencePoint) => {
    let pointNearest;
    // Initially take the first point as the nearestPoint
    let minDistance = distance(geoFieldsCoordinates[0], referencePoint, DistanceUnits.DEGREES);
    pointNearest = geoFieldsCoordinates[0];

    for (let i = 1, len = geoFieldsCoordinates.length; i < len; i++) {
        // Iterate through other points and find the nearest point
        const distanceFromCurrentPoint = distance(geoFieldsCoordinates[i], referencePoint, DistanceUnits.DEGREES);

        if (distanceFromCurrentPoint < minDistance) {
            minDistance = distanceFromCurrentPoint;
            pointNearest = geoFieldsCoordinates[i];
        }
    }
    return pointNearest;
};

/**
 * Calculates perimeter of a given shape from the coordinates
 * @param {Array} coordinates Array of coordinates
 * @param {string} distanceUnit - Unit to be used to calculate perimeter
 * @return {number} Returns the perimeter of the shape rounded to 2 decimal places
 */
export const perimeter = (coordinates, distanceUnit) => {
    const noOfLines = coordinates.length;
    let totalPerimeter = 0;

    // Calculate distance between each coordinates
    for (let i = 0, totLines = noOfLines - 1; i < totLines; i++) {
        const lineLength = distance(coordinates[i], coordinates[i + 1], distanceUnit);
        totalPerimeter += lineLength;
    }
    return totalPerimeter;
};

/**
 * inBounds
 *
 * @private
 * @param {Array.<number>} pt point [x,y]
 * @param {Array.<number>} bound bound coordinates [[left coords], [right coords]
 * @return {boolean} true/false if point is inside bounds
 */
export const isInBounds = (pt, bound) =>
    bound[0][0] <= pt[0] && bound[0][1] <= pt[1] &&
    bound[1][0] >= pt[0] && bound[1][1] >= pt[1];

/**
 * Takes whether a coordinates lies in the coordinates of a polygon
 *
 * @name has
 * @param {Array.<number>} coord1 input polygon
 * @param {Array.<number>} coord2 input point
 * @param {boolean} excludeBoundary True if polygon boundary should be ignored when determining if
 * the point is inside the polygon otherwise false.
 * @return {boolean} `true` if the Point is inside the Polygon; `false` if the Point is not inside the Polygon
 * @example
 * var coord2 = [-77, 44];
 * var coord1 = [
 *   [-81, 41],
 *   [-81, 47],
 *   [-72, 47],
 *   [-72, 41],
 *   [-81, 41]
 * ];
 *
 * returns true;
 */
export const includes = (coord1, coord2, excludeBoundary) => {
    if (!(Array.isArray(coord1) && Array.isArray(coord2))) {
        throw new Error('Wrong input for polygon');
    }
    const boundCoordinates = bounds(coord1);
    let insidePoly = false;

    // Quick elimination if point is not inside bounds
    if ((boundCoordinates
        && isInBounds(coord2, boundCoordinates))
        && inRing(coord2, coord1, !!excludeBoundary)) {
        insidePoly = true;
    }
    return insidePoly;
};

/**
 * Takes which of a set of coordinates fall in the coordinates of a polygon
 *
 * @name within
 * @param {Array.<number>} points input point
 * @param {Array.<number>} polygon input polygon
 * @returns {Array.<number>} points that land within at least one polygon
 * @example
 * points = [
 *     [-46.6318, -23.5523],
 *     [-46.6246, -23.5325],
 *     [-46.6062, -23.5513],
 *     [-46.663, -23.554],
 *     [-46.643, -23.557]
 * ];
 *
 * polygon = [
 *     [-46.653,-23.543],
 *     [-46.634,-23.5346],
 *     [-46.613,-23.543],
 *     [-46.614,-23.559],
 *     [-46.631,-23.567],
 *     [-46.653,-23.560],
 *     [-46.653,-23.543]
 * ];
 */
export const within = (points, polygon) => {
    if (!Array.isArray(points) || !Array.isArray(polygon)) {
        throw new Error('Wrong input');
    }

    const result = [];

    points.map((point) => {
        if (includes(polygon, point, false)) {
            result.push(point);
        }
        return point;
    });
    return result;
};
