import { columnMajor } from '../utils';
import { GeoProps } from '../enums/geo-props';
import { GEOM_FIELD } from '../constants';
import { DimensionSubtype } from '../enums';

const GEOM_DEFAULT = 'geom_default';

/**
 * Returns all the geometry objects and their properties in an array based on the type of the geometry.
 */
const geometryRetriever = {
    [GeoProps.Feature]: obj => [obj],
    [GeoProps.FeatureCollection]: obj => obj.features,
    [GEOM_DEFAULT]: (obj) => {
        const { type, coordinates } = obj;

        return [{
            geometry: {
                type,
                coordinates
            },
            properties: {}
        }];
    }
};

/**
 * Retrieves all the feature objects from a featureCollection or any feature object in an array.
 *
 * @return {Array} Array of features
 */
const getFeatures = (obj) => {
    const { type } = obj;
    const retriever = geometryRetriever[type] || geometryRetriever[GEOM_DEFAULT];

    return retriever(obj);
};

/**
 * Inserts the values of the fields in an array and returns the index of the last field.
 *
 * @param {Array} fieldNames Array of field names
 * @param {Object} fieldInf Information about fields
 * @param {Array} fieldInf.fields Fields array
 * @param {Object} fieldInf.props Properties of each geometry
 * @param {Array} fieldInf.header Field names
 * @param {number} index Index of the field
 *
 * @return {number} Index of the last field.
 */
const insertValues = (fieldNames, fieldInf, index) => {
    let insertionIndex;
    const { fields, props, header } = fieldInf;

    fieldNames.forEach((unitSchema) => {
        if (unitSchema in header) {
            insertionIndex = header[unitSchema];
        } else {
            header[unitSchema] = index++;
            insertionIndex = index - 1;
        }
        fields[insertionIndex] = props[unitSchema];
    });

    return index;
};
/**
 * Parses and converts data formatted in GeoJSON to a manageable internal format.
 *
 * @param {Array.<Object>} arr - The input data formatted in GeoJSON.
 * @return {Array.<Object>} Returns an array of headers and column major data.
 * @example
 *
 * // Sample input data:
 *  {
 *    "type": "FeatureCollection",
 *    "features": [
 *      {
 *        "type": "Feature",
 *        "geometry": {
 *          "type": "Point",
 *          "coordinates": [-2.960847, 53.430828]
 *         },
 *         "properties": {
 *           "name": "Anfield",
 *           "amenity": "Football Stadium"
 *         }
 *       }
 *     ]
 *  }
 *
 *  // Output data
 *  [
 *      ['country', 'population', 'geometry'],
 *      [ ['India'], [1200000000], [{ type: 'Point', coordinates: [-2.960847, 53.430828] }] ]
 *  ]
 */
const GeoJSON = (obj, schema = []) => {
    let i = 0;
    let geoField;

    const header = {};
    const columns = [];
    const push = columnMajor(columns);
    const schemaFieldsName = [];
    const features = getFeatures(obj);

    for (let idx = 0, len = schema.length; idx < len; idx++) {
        const { name, subtype } = schema[idx];
        schemaFieldsName.push(name);
        if (name === GEOM_FIELD && subtype === DimensionSubtype.GEO) {
            geoField = name;
        }
    }

    !geoField && schemaFieldsName.push(GEOM_FIELD);
    features.forEach((feature) => {
        const fields = [];
        const { geometry, properties } = feature;
        const allProps = Object.assign({}, properties, { geometry });

        i = insertValues(schemaFieldsName, { fields, props: allProps, header }, i);
        push(...fields);
    });

    return [Object.keys(header), columns];
};

export default GeoJSON;
