import Geometry from '../geometry';
import { GeomTypes } from '../../enums/geom-types';
import { bounds, center } from '../../utils/geo';
import { distanceToLine, convertLength, isPointOnLineSegment } from './helper';
import Point from '../point';
import { BOUNDARY_POINTS } from '../../constants/geo';
import { UNITS } from '../../enums/distance-factors';

/**
 * Represents LineString.
 *
 * @public
 * @class
 */
export default class LineString extends Geometry {
    /**
     * Initialize a new instance of LineString.
     *
     * @public
     * @param {Object} value - Value type value
     * @param {Array} value.coordinates - The coordinates of the lineString
     * @param {string} value.type - The coordinates of the lineString
     */
    constructor (value) {
        super(value);
        this._realCoordinates = value.coordinates;
        this.coordinates = value.coordinates.map(coordinate => new Point({
            type: GeomTypes.Point,
            coordinates: coordinate
        }));
    }

    /**
     * Get co-ordinates of the lineString.
     *
     * @public
     * @return {Array} array of points
     */
    getCoordinates () {
        return this._realCoordinates;
    }

    /**
     * Get bounds box of the lineString.
     *
     * @public
     * @param {Array} coordinates coordinates of the lineString
     * @return {Array} an array of format [[minX, minY],[maxX, maxY]]
    */
    bounds () {
        let boundsArr = this._cachedBounds;

        if (!boundsArr) {
            const lineStringData = this.coordinates.map(point => point.coordinates);
            boundsArr = this._cachedBounds = bounds(lineStringData);
        }

        return boundsArr;
    }

    /**
     * Returns true if a point is on a line. Accepts a optional parameter to ignore the
     * start and end vertices of the linestring.
     *
     * @name includes
     * @param {Array.<number>} point point Coordinates of form [x, y]
     * @param {boolean} excludeBoundary whether to ignore the start and end vertices.
     * @return {boolean} true/false
     */
    includes(point, excludeBoundary) {
        const lineCoords = this.getCoordinates();

        for (let i = 0; i < lineCoords.length - 1; i++) {
            let ignoreBoundary = '';
            if (excludeBoundary) {
                if (i === 0) { ignoreBoundary = BOUNDARY_POINTS.START; }
                if (i === lineCoords.length - 2) { ignoreBoundary = BOUNDARY_POINTS.END; }
                if (i === 0 && i + 1 === lineCoords.length - 1) { ignoreBoundary = BOUNDARY_POINTS.BOTH; }
            }
            if (isPointOnLineSegment({
                startPoint: lineCoords[i],
                endPoint: lineCoords[i + 1],
                point,
                ignoreBoundary,
            })) {
                return true;
            }
        }
        return false;
    }

     /** Get area of the line string.
     *
     * @public
     * @return {number} area of the line string
     */
    area () {
        return 0;
    }

    /**
     * Get center of the line string.
     *
     * @public
     * @return {Array} center of the bounds
     */
    center () {
        return center(this._realCoordinates);
    }

    /**
     * Returns the minimum distance between from a point
     * minimum distance between the point and any segment of the `LineString`.
     *
     * @name distance
     * @param {Array<number>} pt Coordinates
     * @param {string} units can be anything supported
     * (ex: degrees, radians, miles, or kilometers)
     * @returns {number} distance between point and line
     */
    distance (pt, units = UNITS.KILOMETERS) {
        // validation
        if (!pt && !Array.isArray(pt)) { throw new Error('Enter valid point.'); }
        let distance = Infinity;
        for (let i = 1; i < this._realCoordinates.length; i++) {
            const a = this._realCoordinates[i - 1];
            const b = this._realCoordinates[i];
            const d = distanceToLine(pt, a, b);
            if (d < distance) { distance = d; }
        }
        return convertLength(distance, UNITS.DEGREES, units);
    }
}
