import { latLongPredicates } from '../../../../utils/geo';
import { GEO_ERR_MESSAGES } from '../../../../enums/error-messages';
import Point from '../../../../geo/point';

export const pointPredicates = {
    isArray: coordinates => Array.isArray(coordinates),
    hasMinimumPoints: coordinates => coordinates.length >= 2,
    hasValidLatLong: (coordinates) => {
        const hasValidLatLong = coordinates.every((coord, index) => typeof coord === 'number' &&
            latLongPredicates[index](coord));
        return hasValidLatLong;
    }
};

export const checkPointCoordinates = (coordinates) => {
    let error;

    const isValid = Object.keys(pointPredicates).every((key) => {
        const valid = pointPredicates[key](coordinates);
        if (!valid) {
            error = GEO_ERR_MESSAGES[key];
        }
        return valid;
    });

    if (!isValid) {
        throw new Error(error);
    }

    return true;
};

/**
 * Returns a new point instance if the coordinates are valid. Throws error if coordinates are invalid or the structure
 * is not proper.
 *
 * @param {Object} val
 * @param {string} val.type Type of geometry
 * @param {Array} val.coordinates Coordinates array.
 *
 * @return {Point} Instance of point.
 */
export const pointParser = (val) => {
    const coordinates = val.coordinates;

    checkPointCoordinates(coordinates);

    return new Point(val);
};
