import Geometry from '../geometry';
import { bounds, area, center, includes } from '../../utils/geo';
import { sanitizeConfig } from './helper';
/**
 * Represents Polygon.
 * Coordinates of a Polygon are an array of linear ring coordinate arrays.
 * The first element in the array represents the exterior ring.
 * Any subsequent elements represent interior rings (or holes).
 * https://tools.ietf.org/html/rfc7946#section-3.1.6
 * @public
 * @class
 */
export default class Polygon extends Geometry {
    /**
     * Initialize a new instance of Polygon.
     *
     * @public
     * @param {Object} value - Value type value
     * @param {Array} value.coordinates - The coordinates of the polygon
     * @param {string} value.type - The coordinates of the polygon
     */
    constructor (value) {
        super(value);
        this._realCoordinates = value.coordinates;
        this.coordinates = sanitizeConfig(value.coordinates);
    }

    /**
     * Get co-ordinates of the polygon.
     *
     * @public
     * @return {Array} array of line strings
     */
    getCoordinates () {
        return this._realCoordinates;
    }

    /**
     * Get bounds box of the polygon.
     *
     * @public
     * @return {Array} an array of format [[minX, minY],[maxX, maxY]]
    */
    bounds () {
        let boundsArr = this._cachedBounds;

        if (!boundsArr) {
            const polygon = this.coordinates.map(poly => poly.map(line => line.bounds()));
            const data = polygon.flat(1).flat(1);
            boundsArr = this._cachedBounds = bounds(data);
        }

        return boundsArr;
    }

    /**
     * Get area of the Polygon.
     *
     * @public
     * @return {number} the area of the polygon
     */
    area () {
        const areaObj = [];
        let tempArray;
        const coordinates = this._realCoordinates;
        for (let i = 0, l = coordinates.length; i < l; i++) {
            tempArray = [];
            for (let j = 0, k = coordinates[i].length; j < k; j++) {
                tempArray.push(coordinates[i][j]);
            }
            areaObj.push(area(tempArray));
        }
        let netArea = 0;
        for (let m = 0, n = areaObj.length; m < n; m++) {
            if (m === 0) {
                netArea = areaObj[m];
            } else {
                netArea -= areaObj[m];
            }
        }
        return netArea;
    }

    /**
     * Get center of the Polygon.
     *
     * @public
     * @return {Array} center of the Polygon
     */
    center () {
        return center(this._realCoordinates[0]);
    }

    /**
     * Returns true if a point is in a polygon. Accepts an optional parameter to ignore the
     * start and end vertices of the polygon.
     *
     * @name includes
     * @param {Array.<number>} point point Coordinates of form [x, y]
     * @param {boolean} excludeBoundary whether to ignore the Boundary coordinates.
     * @return {boolean} true/false
     */
    includes(point, excludeBoundary) {
        let result = false;
        const polyCoords = this.getCoordinates();
        const includeMap = polyCoords.map(poly => includes(poly, point, excludeBoundary));
        if (includeMap[0] && includeMap.filter(i => i).length === 1) {
            result = true;
        }
        return result;
    }
}
