import { checkPointCoordinates } from '../point';
import { POLYGON_ERR_MESSAGES, GEO_ERR_MESSAGES } from '../../../../enums/error-messages';
import Polygon from '../../../../geo/polygon';

export const polygonPredicates = {
    hasIdenticalPoints: (coordinates) => {
        const len = coordinates.length;
        return `${coordinates[0]}` === `${coordinates[len - 1]}`;
    },
    hasMinimumFourPoints: coordinates => coordinates.length >= 4
};

/**
 * Checks the coordinates of a linear ring in a polygon and throws error if any invalid values are given.
 *
 * @param {Array} coordinates Array of coordinates.
 */
export const checkLinearRing = (coordinates) => {
    let error;

    coordinates.forEach((coord) => {
        checkPointCoordinates(coord);
    });

    const isValid = Object.keys(polygonPredicates).every((key) => {
        const valid = polygonPredicates[key](coordinates);
        if (!valid) {
            error = POLYGON_ERR_MESSAGES[key];
        }
        return valid;
    });

    if (!isValid) {
        throw new Error(error);
    }

    return true;
};

export const checkPolygonCoordinates = (coordinates) => {
    if (!Array.isArray(coordinates)) {
        throw new Error(GEO_ERR_MESSAGES.isArray);
    }
    if (!coordinates.length) {
        throw new Error(POLYGON_ERR_MESSAGES.hasMinimumCoordinates);
    }
    coordinates.forEach((coordArr) => {
        checkLinearRing(coordArr);
    });
    return true;
};

export const polygonParser = (val) => {
    const coordinates = val.coordinates;

    checkPolygonCoordinates(coordinates);

    return new Polygon(val);
};

