import { GEO_ERR_MESSAGES } from '../../../../enums/error-messages';
import { checkLineStringCoordinates } from '../line-string';
import MultiLineString from '../../../../geo/multi-line-string';

/**
 * Returns a new MultiLineString instance if the coordinates are valid. Throws error if coordinates are invalid or
 * the structure is not proper.
 *
 * @param {Object} val
 * @param {string} val.type Type of geometry
 * @param {Array} val.coordinates Coordinates array.
 *
 * @return {MultiLineString} Instance of MultiLineString.
 */
export const multiLineParser = (val) => {
    const coordinates = val.coordinates;

    if (!Array.isArray(coordinates)) {
        throw new Error(GEO_ERR_MESSAGES.isArray);
    }

    coordinates.forEach((coord) => {
        checkLineStringCoordinates(coord);
    });

    return new MultiLineString(val);
};
