import Geometry from '../geometry';
import Polygon from '../polygon';
import MultiPoint from '../multi-point';
import { bounds } from '../../utils/geo';
import { GeomTypes } from '../../enums/geom-types';

/**
 * Represents MultiPolygon.
 *
 * @public
 * @class
 */
export default class MultiPolygon extends Geometry {
    /**
     * Initialize a new instance of MultiPolygon.
     *
     * @public
     * @param {Object} value - Value type value
     * @param {Array} value.coordinates - The coordinates of the multiPolygon
     * @param {string} value.type - The coordinates of the multiPolygon
     */
    constructor (value) {
        super(value);
        this._realCoordinates = value.coordinates;
        this.coordinates = value.coordinates.map(coordinate => new Polygon({
            type: GeomTypes.Polygon,
            coordinates: coordinate
        }));
    }

    /**
     * Get co-ordinates of the multiPolygon.
     *
     * @public
     * @return {Array} array of Polygons
     */
    getCoordinates () {
        return this._realCoordinates;
    }

    /**
     * Get bounds box of the multi polygon.
     *
     * @public
     * @return {Array} an array of format [[minX, minY],[maxX, maxY]]
    */
    bounds () {
        let boundsArr = this._cachedBounds;

        if (!boundsArr) {
            const polyData = this.coordinates.map(poly => poly.bounds());
            const data = polyData.flat(1);
            boundsArr = this._cachedBounds = bounds(data);
        }
        return boundsArr;
    }

    /**
     * Get area of the multi polygon.
     *
     * @public
     * @return {number} area of the multi polygon
     */
    area () {
        const polyAreaData = this.coordinates.map(poly => poly.area());
        return polyAreaData.reduce((a, b) => a + b, 0);
    }

    /**
     * Get center of the multi polygon.
     *
     * @public
     * @return {number} center of the multi polygon
     */
    center () {
        const polyCenterData = this.coordinates.map(poly => poly.center());
        const multiPoint = new MultiPoint({
            type: GeomTypes.MultiPoint,
            coordinates: polyCenterData
        });
        return multiPoint.center();
    }

    /**
     * Returns true if a point is on one of the polygons in multi-polygons.
     * Accepts a optional parameter to ignore the boundaries of multi polygon
     *
     * @name includes
     * @param {Array.<number>} point point Coordinates of form [x, y]
     * @param {boolean} excludeBoundary whether to ignore boundaries.
     * @return {boolean} true/false
     */
    includes (point, excludeBoundary) {
        return this.coordinates.some(poly => poly.includes(point, excludeBoundary));
    }
}
