import { BOUNDARY_POINTS } from '../../constants/geo';
import { radiansToLength, lengthToRadians } from '../../utils/helper';
import { distance } from '../../utils/geo';
import { UNITS } from '../../enums/distance-factors';

const dot = (u, v) => (u[0] * v[0] + u[1] * v[1]);

/**
 * Returns the distance between a point P on a segment AB.
 *
 * @private
 * @param {Array<number>} p external point
 * @param {Array<number>} a first segment point
 * @param {Array<number>} b second segment point
 * @param {Object} [options={}] Optional parameters
 * @returns {number} distance
 */
export const distanceToLine = (p, a, b) => {
    const v = [b[0] - a[0], b[1] - a[1]];
    const w = [p[0] - a[0], p[1] - a[1]];
    const c1 = dot(w, v);
    if (c1 <= 0) { return distance(p, a, UNITS.DEGREES); }
    const c2 = dot(v, v);
    if (c2 <= c1) { return distance(p, b, UNITS.DEGREES); }
    const b2 = c1 / c2;
    const Pb = [a[0] + (b2 * v[0]), a[1] + (b2 * v[1])];
    return distance(p, Pb, UNITS.DEGREES);
};

/**
* Converts a length to the requested unit.
* Valid units: miles, nauticalmiles, inches, yards, meters, metres, kilometers, centimeters, feet
*
* @param {number} length to be converted
* @param {Units} [originalUnit='kilometers'] of the length
* @param {Units} [finalUnit='kilometers'] returned unit
* @returns {number} the converted length
*/
export const convertLength = (length, originalUnit = UNITS.KILOMETERS, finalUnit = UNITS.KILOMETERS) => {
    if (length < 0) {
        throw new Error('length must be a positive number');
    }
    return radiansToLength(lengthToRadians(length, originalUnit), finalUnit);
};

/**
 * component logic to check if point lies on a line
 *
 * @private
 * @param {Object} params params for the operation
 * @param {Position} startPoint coord pair of start of line
 * @param {Position} endPoint coord pair of end of line
 * @param {Position} point coord pair of point to check
 * @param {boolean|string} ignoreBoundary whether the point is allowed to fall on the line ends.
 * If true which end to ignore.
 * @return {boolean} true/false
 */
export const isPointOnLineSegment = (params) => {
    const { point, startPoint, endPoint, ignoreBoundary } = params;
    const x = point[0];
    const y = point[1];
    const x1 = startPoint[0];
    const y1 = startPoint[1];
    const x2 = endPoint[0];
    const y2 = endPoint[1];
    const dxc = point[0] - x1;
    const dyc = point[1] - y1;
    const dxl = x2 - x1;
    const dyl = y2 - y1;
    const cross = dxc * dyl - dyc * dxl;
    let returnData = false;

    if (cross !== 0) {
        returnData = false;
    } else if (!ignoreBoundary) {
        if (Math.abs(dxl) >= Math.abs(dyl)) {
            returnData = dxl > 0 ? x1 <= x && x <= x2 : x2 <= x && x <= x1;
        }
        returnData = dyl > 0 ? y1 <= y && y <= y2 : y2 <= y && y <= y1;
    } else if (ignoreBoundary === BOUNDARY_POINTS.START) {
        if (Math.abs(dxl) >= Math.abs(dyl)) {
            returnData = dxl > 0 ? x1 < x && x <= x2 : x2 <= x && x < x1;
        } else {
            returnData = dyl > 0 ? y1 < y && y <= y2 : y2 <= y && y < y1;
        }
    } else if (ignoreBoundary === BOUNDARY_POINTS.END) {
        if (Math.abs(dxl) >= Math.abs(dyl)) {
            returnData = dxl > 0 ? x1 <= x && x < x2 : x2 < x && x <= x1;
        } else {
            returnData = dyl > 0 ? y1 <= y && y < y2 : y2 < y && y <= y1;
        }
    } else if (ignoreBoundary === BOUNDARY_POINTS.BOTH) {
        if (Math.abs(dxl) >= Math.abs(dyl)) {
            returnData = dxl > 0 ? x1 < x && x < x2 : x2 < x && x < x1;
        } else {
            returnData = dyl > 0 ? y1 < y && y < y2 : y2 < y && y < y1;
        }
    }
    return returnData;
};
