import { GeomTypes } from '../../enums/geom-types';
import LineString from '../line-string';

/**
 * sanitize the config of the coordinate to change it to
 * an array of line strings
 * @param {Array} value array of original values
 * @return {Array} array of Line Strings
 */
export const sanitizeConfig = (value) => {
    const coordinates = [];
    const type = GeomTypes.LineString;

    for (let i = 0; i < value.length; i++) {
        coordinates[i] = [];
        for (let j = 0; j < value[i].length - 1; j++) {
            coordinates[i].push(new LineString({
                type, coordinates: [value[i][j], value[i][j + 1]]
            }));
        }
    }
    return coordinates;
};
