import GeoField from '../geo-field';
import { DimensionSubtype } from '../../enums';

/**
 * Represents binned geo field subtype.
 *
 * @public
 * @class
 * @extends Dimension
 */
export default class BinnedGeoField extends GeoField {
    /**
     * Initialize a new instance.
     *
     * @public
     * @param {PartialField} partialField - The partialField instance which holds the whole data.
     * @param {string} rowDiffset - The data subset definition.
     */
    constructor (partialField, rowDiffset) {
        super(partialField, rowDiffset);

        this._cachedBounds = null;
    }

    subtype () {
        return DimensionSubtype.BINNED_GEO;
    }

    shapeType () {
        return this.partialField.schema.shapeType;
    }

    binSize () {
        return this.partialField.schema.binSize;
    }
}
