import { checkPolygonCoordinates } from '../polygon';
import { GEO_ERR_MESSAGES } from '../../../../enums/error-messages';
import MultiPolygon from '../../../../geo/multi-polygon';

/**
 * Returns a new MultiPolygon instance if the coordinates are valid. Throws error if coordinates are invalid or
 * the structure is not proper.
 *
 * @param {Object} val
 * @param {string} val.type Type of geometry
 * @param {Array} val.coordinates Coordinates array.
 *
 * @return {MultiPolygon} Instance of multipolygon.
 */
export const multiPolygonParser = (val) => {
    const { coordinates } = val;

    if (!Array.isArray(coordinates)) {
        throw new Error(GEO_ERR_MESSAGES.isArray);
    }

    coordinates.forEach((coordArr) => {
        checkPolygonCoordinates(coordArr);
    });

    return new MultiPolygon(val);
};
